body {
  background: url("./fondo.jpg") 0 0 no-repeat;
}

.App {
  margin: 10% 5%;
  display: grid;
  grid-template-columns: repeat(3, 1fr);
  font-size: 20px;
  font-weight: bold;
}

.subContenedor {
  display: flex;
  flex-direction: column;
  justify-content: center;
  text-align: center;
  align-items: center;
}

.controlTiempo {
  min-width: 70px;
  min-height: 45px;
  font-size: 25px;
  font-weight: bold;
  background:lightslategray;
  margin-bottom: 5%;

}

input {
  border: 0;
  font-size: 25px;
  font-weight: bold;
  width: 70px;
  text-align: center;
}

.timer {
  font-size: 30px;
  color: blue;
}

.botonImportante {
  width: 100%;
  max-width: 200px;
  min-height: 45px;
  margin-bottom: 10%;
  font-size: 20px;
  font-weight: bold;
  background: lightgreen;
  border: 1px solid lightgreen;
  box-shadow: 5px 5px 3px 3px darkgreen;
}

.reset {
  background: lightcoral;
  border: 1px solid lightcoral;
  box-shadow: 5px 5px 3px 3px darkred;

}

#timer-label {
  padding: 5px;
  border: 1px solid blue;
}

.creditos {
  position: absolute;
  bottom: 0;
  left: 0;
  font-size: 9px;
}